  const flow = {
    start: {
      message: "Welcome to Travel Tales! What is your name?",
      path: "ask_region",
      function: (params) => setForm({ ...form, name: params.userInput }),
    },
    ask_region: {
      message: (params) => {
        if (isFirstInteraction && form.name) {
          // Modify this line
          setIsFirstInteraction(false); // Add this line
          return `Nice to meet you, ${params.userInput}! Choose a region to explore:`;
        }
        return "Choose a region to explore:";
      },
      options: () => {
        return Object.keys(regions).map((region) => {
          // Capitalize the first letter of each word in the region name
          return region
            .split("-")
            .map((word) => word.charAt(0).toUpperCase() + word.slice(1))
            .join(" ");
        });
      },
      path: "select_region",
    },
    select_region: {
      message: (params) => {
        // Normalize the user input to match the keys in the regions object
        const regionKey = params.userInput.toLowerCase().replace(" ", "-");
        setForm({ ...form, region: regionKey });
        return `Great choice! Here are some states in ${
          params.userInput
        }: ${regions[regionKey].join(", ")}. Select one.`;
      },
      path: "select_state",
    },
    select_state: {
      message: (params) => {
        setForm({ ...form, state: params.userInput });
        return `Awesome! Do you want to visit a beach, mountains, or cultural sites in ${params.userInput}?`;
      },
      options: ["Beach", "Mountains", "Cultural"],
      path: "select_category",
    },
    select_category: {
      message: (params) => {
        const { state } = form;
        const category = params.userInput.toLowerCase();
        setForm({ ...form, category: params.userInput });

        let places = []; // Initialize as an empty array

        if (categories[category] && categories[category][state]) {
          places = categories[category][state];
        }

        if (!places || places.length === 0) {
          return `Sorry, there are no recommended places in ${state} for ${params.userInput}.`;
        } else {
          return `Here are some recommended places in ${state} for ${
            params.userInput
          }: ${places.join(", ")}.`;
        }
      },
      path: (params) => {
        const { state } = form;
        const category = params.userInput.toLowerCase();

        if (categories[category] && categories[category][state]) {
          return "ask_book_tickets";
        } else {
          return "ask_more";
        }
      },
    },
    ask_book_tickets: {
      // New step
      message: "Do you want to book tickets now?",
      options: ["Yes", "No"],
      path: (params) => {
        if (params.userInput.toLowerCase() === "yes") {
          return "show_tickets"; // If "Yes", go to ticket link
        } else {
          return "ask_more"; // If "No", continue the process
        }
      },
    },
    show_tickets: {
      // New step
      message: `You can book your tickets here: ${ticketLink}`,
      path: "ask_more",
    },
    ask_more: {
      message: "Would you like to explore more destinations?",
      options: ["Yes", "No"],
      path: (params) => {
        if (params.userInput.toLowerCase() === "yes") {
          return "ask_region";
        }
        return "end";
      },
    },
    end: {
      message: "Thanks for using Travel Tales! Have a safe journey!",
    },
  };
